@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  background-color: #1a1a1c;
  color: #fff
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;

}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
  font-family: 'Poppins', sans-serif
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  );
  color: #e2e2e2
}

.coin-input::placeholder {
  color: #e2e2e2
}

.coin-container {
  display: flex;
  justify-content: center;
}

.coin-row {
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  height: 80px;
  border-bottom: 1px solid #d7d7d7;
  width: 1000px;
}

.coin {
  display: flex;
  align-items: center;
  padding-right: 24px;
  min-width: 300px;

}

.coin h1 {
 font-size: 16px;
 width: 150px;

}

.coin img {
  height: 30px;
  width: 30px;
  margin-right: 10;
}

.coin-symbol {
text-transform: uppercase;
}

.coin-data {
  display: flex;
  text-align: center;
  justify-content: space-between;
  width: 100%;
}

.coin-price {
  width: 110px;
  margin-left: 25px;
}

.coin-volume {
  width: 200px;
  margin-left: 60px;
}

.coin-marketcap {
  width: 240px;
}

.coin-percent {
  width: 80%;
}

.red {
  color: #f00606;
}

.green {
  color: #11d811
}
@media screen and (max-width: 1500px) {
 * {
   font-size: 15px;
 }
 .coin-row {
   width: 700px;
 }
 .coin-data {
   width: 80%;
 }
 .coin {
   margin-right: 15px;
   min-width: 100px;
 }
 .coin-marketcap {
   width: 150px
 }
 .coin-volume{
   width: 150px;
 }
 .coin-percent {
   width: 40%
 }
 .coin-text {
   font-size: 30px;
 }
 
}
@media screen and (max-width: 1000px) {
  *{
    font-size: 10px;
  }
  .coin {
    padding-right: 5px;
    min-width: 50px;
    z-index: 99;
  }
  .coin h1{
    font-size: 11px;
    margin-right: 10px;
  }
  .coin-row {
    width: 430px;
  }
  .coin-price {
    margin-left: 50px;
  }
  .coin-volume {
    margin-left: 20px;
  }
  .coin-text {
    font-size: 20px;
  }
}